<template>
  <v-container fluid fill-height>
    <v-row justify="space-around">
      <v-card width="400" class="pa-4 rounded-xl">
        <v-icon large class="icon-lock">
          {{ is_lock_open ? "mdi-lock-open-variant" : "mdi-lock" }}</v-icon
        >
        <!-- Countdown -->
        <v-card-title
          v-if="count_effort === 3"
          class="red--text text--darken-2 justify-center"
          >{{ counter }}</v-card-title
        >
        <!-- Login form -->
        <form class="mb-2" @submit.prevent="submit()" v-else>
          <v-card-text class="pb-0">
            <v-alert
              v-if="msg_error.length > 0"
              dense
              text
              type="error"
              class="mb-0"
              >{{ msg_error }}</v-alert
            >
          </v-card-text>
          <v-card-text>
            <v-text-field
              v-model="login"
              :error-messages="loginErrors"
              :label="$t('message.5')"
              required
              clearable
              autofocus
              @input="$v.login.$touch()"
              @blur="$v.login.$touch()"
            ></v-text-field>
            <v-text-field
              v-model="password"
              :error-messages="passwordErrors"
              :label="$t('message.6')"
              required
              clearable
              :append-icon="see_pass ? 'mdi-eye' : 'mdi-eye-off'"
              :type="see_pass ? 'text' : 'password'"
              @click:append="see_pass = !see_pass"
              @input="$v.password.$touch()"
              @blur="$v.password.$touch()"
            ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn dark small depressed type="submit">
              <spam>{{ $t("message.9") }}</spam>
            </v-btn>
            <v-btn
              dark
              small
              depressed
              color="red darken-2"
              class="ml-4"
              @click="clear()"
            >
              <spam class="">{{ $t("message.10") }}</spam>
            </v-btn>
          </v-card-actions>
        </form>
      </v-card>
    </v-row>
  </v-container>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import { validationMixin } from "vuelidate";
import { required } from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],

  validations: {
    login: { required },
    password: { required },
  },

  name: "LoginPage",

  data: () => ({
    login: "",
    password: "",
    msg_error: "",
    is_lock_open: false,
    // For protect against woodpeckers
    count_effort: 0,
    countdown: 10,
    counter: 0,
    see_pass: false,
  }),

  computed: {
    ...mapState(["isAuthenticated"]),

    loginErrors() {
      const errors = [];
      if (!this.$v.login.$dirty) return errors;
      !this.$v.login.required && errors.push(this.$t("message.7"));
      return errors;
    },
    passwordErrors() {
      const errors = [];
      if (!this.$v.password.$dirty) return errors;
      !this.$v.password.required && errors.push(this.$t("message.8"));
      return errors;
    },
  },

  methods: {
    ...mapMutations(["setIsAuthenticated"]),
    ...mapActions("popUpMsgs", ["runShowMsg"]),
    submit() {
      // Form validation
      this.$v.$touch();
      if (this.$v.$invalid) {
        this.submitStatus = "ERROR";
      } else {
        // Login Request
        const payload = {
          login: this.login,
          password: this.password,
        };
        this.axios
          .post("/admin/login", payload)
          .then((response) => {
            const data = response.data;
            if (data.is_authenticated) {
              this.msg_error = "";
              this.is_lock_open = true;
              setTimeout(() => {
                this.setIsAuthenticated(true);
              }, 500);
            } else {
              this.is_lock_open = false;
              this.setIsAuthenticated(false);
              this.msg_error = this.$t("message.27");
              // Protect against woodpeckers
              if (++this.count_effort === 3) {
                this.counter = this.countdown;
                const timer = window.setInterval(() => {
                  if (--this.counter === 0) {
                    window.clearInterval(timer);
                    this.countdown += Math.floor(Math.random() * 20 + 1);
                    this.msg_error = "";
                    this.clear();
                    this.count_effort = 0;
                  }
                }, 1000);
              }
            }
          })
          .catch((error) => {
            this.msg_error = this.$t("message.27");
            console.log(error);
          });
      }
    },
    // Clear form
    clear() {
      this.$v.$reset();
      this.login = "";
      this.password = "";
      this.msg_error = "";
    },
  },

  created() {
    if (this.isAuthenticated) {
      this.$router.push({ name: "home" });
    }
  },
};
</script>

<style scoped>
.icon-lock {
  position: absolute;
  top: -18px;
  left: 50%;
  margin-left: -18px;
}
</style>
